import { Component } from '@angular/core';
import { Platform, AlertController } from '@ionic/angular';
import { LoadingController, ToastController } from '@ionic/angular';
import { Router } from '@angular/router';
import { AngularFireAuth } from '@angular/fire/auth';
import { Logger } from '../../providers/logger/logger';
import { Storage } from '@ionic/storage';
import {environment} from '../config/environment';



@Component({
  selector: 'app-verifyemail',
  templateUrl: './verifyemail.page.html',
  styleUrls: ['./verifyemail.page.scss'],
})
export class VerifyemailPage {
  email: string = '';
  password: string = '';
  error: string = '';
  htmltoprint: string = '';
  image: number;
  constructor(private fireauth: AngularFireAuth, 
          private router: Router, 
          private logger: Logger, 
          private storage: Storage,

	  private toastController: ToastController, private platform: Platform, public loadingController: LoadingController,
    public alertController: AlertController) {

  console.log(environment.storageuniq+'htmltoprint'  );

    this.storage.get(environment.storageuniq+'htmltoprint').then(data=> {
  console.log(data );
        if(data) {
        this.htmltoprint = data;
        } else {
        }
     });


  }



  async presentToast(message, show_button, position, duration) {
    const toast = await this.toastController.create({
      message: message,
      showCloseButton: show_button,
      position: position,
      duration: duration
    });
    toast.present();
  }

  print(): void {
    let printContents, popupWin;
    printContents = document.getElementById('print-section').innerHTML;
    popupWin = window.open('', '_blank', 'top=0,left=0,height=100%,width=auto');
    popupWin.document.open();
    popupWin.document.write(`
      <html>
        <head>
          <title>Print tab</title>
          <style>
          //........Customized style.......
          </style>
        </head>
    <body onload="window.print();window.close()">${printContents}</body>
      </html>`
    );
    popupWin.document.close();
    this.back();
  }
  back(): void {
//    this.router.navigateByUrl("..");
  }
}
